$font-size-01: 14px;
.main-section {
  background-color: #f6f7fc;
  margin: 0;
}
.left-sidebar {
  background-color: #f6f7fc;
  margin-right: 0;
  color: #fff;

  .nav-panel {
    background-color: #3f65dd26;
    padding: 10px;
    margin: 0 15px 0 15px;
    border-radius: 12px;
    .guest-nav-panel {
      .panel-item:not(:last-child) {
        margin-bottom: 15px;
        color: #171c2f;
      }
      .panel-item {
        .base-navlink {
          color: #3f65dd;
          font-size: 8px;
          font-weight: bold;
        }
      }
    }
  }
}
