// Media breakpoints
$breakpoint-xs: 480px;
$breakpoint-sm: 576px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1200px;

.signup-page {
  height: 100vh;
  .signup-container {
    text-align: center;
    margin: 1.8rem 0;
    @media (max-width: $breakpoint-md) {
      margin: 0rem 0;
    }
    .signup-bg {
      border-radius: 0 36px 36px 0;
      @media (max-width: $breakpoint-md) {
        border-radius: 0 0 0 0;
      }
    }
    .signup-inner {
      padding: 45px;
      @media (max-width: $breakpoint-md) {
        padding: 0px;
      }
      .signup-form {
        border: 1px solid #00ff1199;
        border-radius: 36px;
        max-width: 700px;
        margin: 0 auto;
        @media (max-width: $breakpoint-md) {
          border: none;
          border-radius: 0px;
        }
        .logo {
          margin-top: 14px;
          margin-bottom: 25px;
          img {
            width: 140px;
          }
        }
        form {
          padding: 1.6rem;
          input {
            background-color: #f6f6f8;
            height: 42px;
            border: 1px solid #00ff08a6;
            border-radius: 8px;
            font-size: 14px;
            width: 100%;
            text-indent: 15px;
            color: #9e9e9e;
            margin-bottom: 25px;
          }
          input[type="password"] {
            font-size: 14px;
            height: 42px;
            border-radius: 8px;
          }
          input[type="password"] {
            border: none;
            margin: 0;
            height: 40px;
          }
          .ant-input-password {
            background-color: #f6f6f8;
            border-radius: 8px;
            margin-bottom: 25px;
            height: 42px;
            padding: 0 10px 0 0;
            border: 1px solid #00ff08a6;
            .ant-input {
              border: none;
              margin: 0;
            }
          }
          .select-wrapper {
            margin-bottom: 25px;
          }
          select,
          .vn-select,
          .ant-select {
            background-color: #f6f6f8;
            display: block;
            border: 1px solid #00ff08a6;
            border-radius: 8px;
            height: 42px;
            color: #9e9e9e;
            font-size: 14px;
            .ant-select-selector {
              // padding-top: 14px;
              border-radius: 8px !important;
              height: 100% !important;
              .ant-select-selection-search-input {
                color: #9e9e9e !important;
                border: none;
                margin: 0;
              }
            }
            .ant-select-selection-placeholder {
              position: absolute;
              top: 5px;
              left: 0;
              right: 0;
            }
            .ant-select-selection-item {
              top: 5px;
            }
            .ant-select-arrow {
              .anticon {
                color: #56fc5b !important;
                font-size: 14px !important;
              }
            }
          }
          .vn-txtarea {
            position: relative;
            textarea {
              background-color: #f6f6f8;
              border: 1px solid #00ff08a6;
              border-radius: 8px;
              height: 114px;
              border-radius: 8px;
              color: #9e9e9e;
              font-size: 14px;
              width: 100%;
              display: inline-block;
              margin-bottom: 25px;
            }
            .error {
              bottom: 3px;
            }
          }
          .vn-search-select {
            background-color: #f6f6f8;
            border-radius: 8px;
            .ant-select-selection-search-input {
              background-color: transparent !important;
              height: 100%;
            }
          }

          .error {
            color: red;
            position: absolute;
            bottom: 2px;
          }
          .error-date {
            color: red;
            position: absolute;
            bottom: -23px;
          }
        }
        .describe-option {
          .ant-radio-group {
            .ant-radio-button-wrapper {
              margin: 0 2% 2% 0;
              border: none;
              float: left;
              width: 23%;
              padding: 0 10px;
              font-size: 11px;
              font-weight: bold;
              height: 28px;
              border-radius: 3px;
              background-color: #bebebe;
              color: #58768f;
              display: inline-block;
              &::before {
                background-color: transparent;
              }
              &::after {
                background-color: transparent;
              }
              &:hover {
                background-color: #969696;
                color: #aabece;
              }
              @media (max-width: $breakpoint-md) {
                margin: 0 3% 3% 0;
                width: 22%;
                padding: 0 10px;
                height: 48px;
                line-height: 48px;
              }
              @media (max-width: $breakpoint-xs) {
                margin: 0 5% 4% 0;
                width: 45%;
              }
            }
          }
        }
        .genere-select {
          text-align: left;
          margin-bottom: 25px;
          label {
            color: #9e9e9e;
            font-size: 18px;
            padding: 10px;
          }
          .ant-checkbox-group {
            label {
              font-size: 14px;
              width: 23%;
              padding: 5px;
              @media (max-width: $breakpoint-xl) {
                width: 30%;
              }
              @media (max-width: $breakpoint-lg) {
                width: 40%;
              }
            }
            .ant-checkbox-inner {
              background-color: transparent;
              border-radius: 100%;
              border: 2px solid #2d60ef;
              width: 14px;
              height: 14px;
            }
            .ant-checkbox-checked {
              .ant-checkbox-inner {
                background: #2dceef 0% 0% no-repeat padding-box;
                color: red;
              }
            }
          }
        }
        .form-submt-btn {
          display: block;
          border: none;
          font-size: 14px;
          font-weight: bold;
          border-radius: 8px;
          width: 100%;
          height: 42px;
          margin-bottom: 14px;
          background: transparent
            linear-gradient(180deg, #4282e2 0%, #74ef48 100%) 0% 0% no-repeat
            padding-box;
          &:hover {
            background: rgb(116, 239, 72);
            background: linear-gradient(
              180deg,
              rgba(66, 130, 226, 1) 1%,
              rgb(100, 165, 76) 80%
            );
          }
        }
        .form-clear-btn {
          display: block;
          border: none;
          font-size: 14px;
          font-weight: bold;
          border-radius: 8px;
          width: 100%;
          height: 42px;
          background: transparent;
          border: 2px solid #009389;
          color: #009389;
        }
      }
    }
  }
  .social-login {
    background-color: #003463;
    border-radius: 0 0 36px 36px;
    margin-top: 14px;
    padding: 40px 0 30px 0;
    @media (max-width: $breakpoint-md) {
      border-radius: 0 0 0 0;
    }
    p {
      color: #40727a;
      font-size: 16px;
      margin: 0;
    }
    ul {
      list-style-type: none;
      padding: 0;
      display: inline-block;
      margin: 0 auto;
      li {
        float: left;
        margin-top: 5px;
        .btn {
          border-radius: 4px;
          height: 36px;
          display: inline-block;
          padding: 6px 0;
          width: 148px;
          span {
            img {
              width: 28px;
            }
          }
        }
        .fb-btn {
          color: #fff;
          background-color: #4267b2;
          margin-right: 15px;
        }
        .ggl-btn {
          background-color: #ffffff;
          color: #121212;
        }
      }
    }
    .dots {
      font-size: 25px;
      color: #b3c2d0;
      line-height: 10px;
    }
  }

  .login-description {
    height: 100vh;
    @media (max-width: $breakpoint-md) {
      display: none;
    }
    .login-description-wrapper {
      display: flex;
      align-items: center;
      height: 100%;
      h1 {
        font-family: "Segoe UI", serif;
        margin: 0 0 0 50px;
        font-size: 72px;
        line-height: 96px;
        color: #0a2133;
        @media (max-width: $breakpoint-xl) {
          font-size: 42px;
          line-height: 72px;
        }
        @media (max-width: $breakpoint-lg) {
          font-size: 32px;
          line-height: 52px;
        }
        span {
          text-transform: uppercase;
        }
      }
    }
  }
}
