.trending-artist-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #d9e0fa;
  border-radius: 12px;
  padding: 15px 0 15px 0;

  .panel-topic {
    font-size: 16px;
    margin-bottom: 15px;
    font-weight: bold;
    color: #2c4ebe;
  }
  .list-container {
    // background-color: #171c2f;
    padding: 15px;
    .ant-list-item {
      float: left;
      width: 50%;
      padding: 5px;
      .artist-tile {
        border: 2px solid #b3c3ff;
        border-radius: 5px;
        // box-shadow: 1px 1px 3px #9ab2f8;
        &:hover {
          background-color: #171c2f;
        }
        a {
          .avatar {
            border: 1px solid #ffffff;
            margin: 0;
          }
        }
        .username {
          color: #2c4ebe;
          font-weight: bold;
          font-size: 14px;
          text-align: center;
        }
        .follow-btn {
          button {
            border-radius: 6px;
          }
        }
      }
    }
  }

  .scroll-btn {
    background-color: #2c4ebe;
    color: #fff;
    &:hover {
      -moz-box-shadow: 1px 1px 3px 5px #c7c7c7;
      -webkit-box-shadow: 1px 1px 3px 5px #c7c7c7;
      box-shadow: 1px 1px 5px 3px #c7c7c7;
    }
  }
  .scoll-up-arrow {
    margin-bottom: 10px;
  }
  .scoll-down-arrow {
    margin-top: 10px;
  }

  .list-container {
    width: 100%;
    max-height: 60vh;
    overflow: scroll;

    /* Hide scrollbar for IE, Edge and Firefox */
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }

  /* Hide scrollbar for Chrome, Safari and Opera */
  .list-container::-webkit-scrollbar {
    display: none;
  }
}
