.ant-layout-header.header {
  line-height: 60px;
  height: auto;
}
header {
  background-color: #3f65dd;
  //   display: inline-block;
  //   width: 100%;
  .nav-logo {
    img {
      width: 50px;
    }
  }
  .main-menu {
    ul {
      background-color: #3f65dd;
      color: #000000;
      text-align: center;
      font-size: 16px;
      font-weight: 500;
      border: none;
      .nav-link {
        padding: 0 10px;
      }
    }
    .ant-menu {
      .ant-menu-item {
        margin: 0;
        top: 1px;
        border: none;
        &:hover {
          border: none;
          &::after {
            content: "";
            background-color: rgba(0, 0, 0, 0.5);
            width: 100%;
            position: absolute;
            left: 0;
            bottom: 0px;
            height: 4px;
          }
        }
      }
      .nav-link {
        padding: 0 20px;
      }
      .ant-menu-item-selected {
        .nav-link {
          color: rgba(0, 0, 0, 0.5);
          font-weight: 500;
          //   border: 3px solid #fff;
          &::after {
            content: "";
            background-color: rgba(0, 0, 0, 0.5);
            width: 100%;
            position: absolute;
            left: 0;
            bottom: 0px;
            height: 4px;
          }
        }
      }
    }
  }
  .ant-menu-horizontal {
    // border-bottom: none;
  }
}

.log-menu {
  ul {
    // background-color: #3f65dd;
    // border: none;
  }
}

.vn-main-btn {
  background: transparent linear-gradient(180deg, #4282e2 0%, #74ef48 100%) 0%
    0% no-repeat padding-box;
  height: auto;
  display: inline;
  padding: 5px 15px;
  border-radius: 8px;
  &:hover {
    color: #000000;
    background: transparent linear-gradient(180deg, #4282e2 0%, #74ef48 140%) 0%;
  }
}
