.login-page {
  .login-container {
    height: 100vh;
    text-align: center;
    display: flex;
    align-items: center;
    .form-wrapper {
      height: 90vh;
      width: 100%;
      display: flex;
      align-items: center;
      border-radius: 0 36px 36px 0;
      background-size: contain;
      .form-container {
        border: 1px solid #31725c;
        background-color: #31725c4a;
        border-radius: 10px;
        width: 400px;
        margin: 0 auto;
        form {
          padding: 0 20px;
        }
        .logo {
          margin-bottom: 20px;
          padding-top: 20px;
          a {
            img {
              width: 100px;
              margin: 0 auto;
            }
          }
        }
        input[type="text"],
        input[type="password"] {
          background-color: #f6f6f8;
          text-align: center;
          margin-bottom: 8px;
          font-size: 14px;
          height: 42px;
          border-radius: 8px;
        }
        input[type="password"] {
          height: 40px;
          text-indent: 20px;
        }
        .ant-input-affix-wrapper {
          background-color: #f6f6f8;
          margin: 8px 0 8px 0;
          position: relative;
          display: inline-block;
          width: 100%;
          min-width: 0;
          height: 42px;
          padding: 0px 15px 0px 10px;
          color: rgba(0, 0, 0, 0.85);
          font-size: 20px;
          line-height: 1.5715;
          -webkit-transition: all 0.3s;
          transition: all 0.3s;
          display: -ms-inline-flexbox;
          display: inline-flex;
          border-radius: 8px;
        }
        .ant-input-password {
          input[type="text"] {
            height: 40px;
            text-indent: 20px;
          }
        }
        .error {
          color: red;
          margin-bottom: 0;
        }
        .fp-link {
          text-align: right;
          margin-top: 5px;
          a {
            color: #3f66de;
          }
        }
        .login-btn {
          margin-top: 10px;
          button {
            width: 100%;
            border-radius: 8px;
            height: 42px;
            cursor: pointer;
            color: #2b2b2b;
            font-size: 14px;
            font-weight: bold;
            background: rgb(66, 130, 226);
            background: transparent
              linear-gradient(180deg, #4282e2 0%, #74ef48 100%) 0% 0% no-repeat
              padding-box;
            border: 1px solid #001e93;
            &:hover {
              background: rgb(116, 239, 72);
              background: transparent
                linear-gradient(180deg, #4282e2 30%, #74ef48 150%) 0% 0%
                no-repeat padding-box;
            }
          }
        }
        .remember-me {
          text-align: left;
          color: #bdbdbd;
          font-size: 18px;
          font-weight: 600;
          margin-top: 10px;
          button {
            margin-left: 2px;
            background-color: #7b7d80;
            .ant-switch-handle {
              &::before {
                width: 18px;
                height: 18px;
                top: -4px;
                left: -2px;
                background: rgb(66, 130, 226);
                background: linear-gradient(
                  180deg,
                  rgba(66, 130, 226, 1) 20%,
                  rgb(127, 236, 87) 100%
                );
              }
            }
          }
        }
        .social-login {
          background-color: #003463;
          border-radius: 0 0 10px 10px;
          margin-top: 20px;
          padding: 40px 0 30px 0;
          p {
            color: #40727a;
            font-size: 16px;
            margin: 0;
          }
          ul {
            list-style-type: none;
            padding: 0;
            display: inline-block;
            margin: 0 auto;
            li {
              float: left;
              margin-top: 5px;
              .btn {
                border-radius: 4px;
                height: 36px;
                display: inline-block;
                padding: 6px 0;
                width: 148px;
                span {
                  img {
                    width: 28px;
                  }
                }
              }
              .fb-btn {
                color: #fff;
                background-color: #4267b2;
                margin-right: 15px;
              }
              .ggl-btn {
                background-color: #ffffff;
                color: #121212;
              }
            }
          }
          .dots {
            font-size: 25px;
            color: #b3c2d0;
            line-height: 10px;
          }
        }
      }
    }
  }

  .login-description {
    height: 100vh;
    .login-description-wrapper {
      display: flex;
      align-items: center;
      height: 100%;
      h1 {
        font-family: "Segoe UI", serif;
        margin: 0 0 0 50px;
        font-size: 72px;
        line-height: 96px;
        color: #0a2133;
        span {
          text-transform: uppercase;
        }
      }
    }
  }
}

