.timeline-post {
  $border: 1px solid rgb(240, 240, 240);
  // $border: 1px solid red;
  width: 100%;
  margin: 0 30px;
  border: $border;
  border-radius: 8px;
  background-color: white;
  padding: 15px;
  margin-top: 10px;

  .post-body {
    padding: 10px 10px 0px 10px;
    min-width: 660px;
  }

  .post-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: $border;
    padding: 8px 0px;

    .timestamp {
      margin: 0;
    }
  }

  .post-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0px 10px 10px 10px;
    border-radius: 0 0 5px 5px;
    margin: 0 10px;
    background-color: #000;

    .action-btn-group {
      .repost {
        margin-right: 5px;
      }
    }
  }
  .action-btn-group {
    button {
      background-color: transparent;
      // background-color: #1890ff;
      color: #1890ff;
      border: none;
      &:hover {
        color: #fff;
      }
    }
  }
}
